.item {
    display: flex;
    flex-direction: row;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
}

.status {
    width: 18px;
    height: 18px;
    display: block;
    border-radius: 50%;
    margin-top: auto;
    margin-bottom: auto;
}

.avatar {
    width: 130px;
    margin-left: 40px;
}

.name {
    color: #222222;
    margin-top: 20px;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 1.16;
    letter-spacing: 0.01em;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 50px;
}