.statistics {
    background-color: #6c8d49;
    width: 580px;
    border-radius: 8px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
        0px 2px 1px rgba(0, 0, 0, 0.2);
}

.title {
    color: #222222;
    padding-top: 20px;
    padding-bottom: 20px;
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 1.16;
    text-align: center;
    letter-spacing: 0.01em;
}

.list {
    display: flex;
}

.item {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: calc(580px / 5);
    border-radius: 5px;
}

.percentage {
    font-weight: 300;
    font-size: 24px;
    line-height: 1.6;
}